<template>
  <div class="wiki-register-warp">
    <div class="wiki-register">
      <div class="wiki-register-content">
        <div class="register-content-email">
          邮箱：
          <el-form
            :model="dynamicValidateForm"
            ref="dynamicValidateForm"
            label-width="100px"
            class="demo-dynamic"
          >
            <el-form-item
              prop="email"
              :rules="[
                        { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                        { type: 'email', message: '请输入正确的邮箱格式', trigger: 'change' }
                    ]"
              class="wiki-modify-main-select-email"
            >
              <el-input v-model="dynamicValidateForm.email"></el-input>
            </el-form-item>
          </el-form>
        </div>

        <div class="register-content-username">
          用户名：
          <el-input v-model.trim="username" placeholder="请输入用户名"></el-input>
        </div>

        <el-form
          :model="ruleForm2"
          status-icon
          :rules="rules2"
          ref="ruleForm2"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item prop="pass">
            密码：
            <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item prop="checkPass">
            再次输入密码：
            <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>

        <a href="#/login" class="register-content-go-login">已有账号？登录</a>
        <el-button type="primary" @click="register">注册</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import * as ajax from "@/api"; //请求
import publicheader from "@/components/publicHeader"; //公共头

export default {
  data() {
    var validatePass = (rule, value, callback) => {
      if (value.length < 6) {
        callback(new Error("密码需要超过6位数"));
      }
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm2.checkPass !== "") {
          this.$refs.ruleForm2.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        this.password = "";
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm2.pass) {
        this.password = "";
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
        this.password = value;
      }
    };
    return {
      phonenumber: "",
      password: "",
      username: "",
      ruleForm2: {
        pass: "",
        checkPass: ""
      },
      rules2: {
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }]
      },
      errorMessage: "",
      dynamicValidateForm: {
        domains: [
          {
            value: ""
          }
        ],
        email: ""
      },
    };
  },
  methods: {
    register() {
      var reg = new RegExp(
        "^[a-z0-9A-Z]+[- | a-z0-9A-Z . _]+@([a-z0-9A-Z]+(-[a-z0-9A-Z]+)?\\.)+[a-z]{2,}$"
      );

      if (this.password == "") {
        alert("密码有误");
        return;
      } else if (this.username == "") {
        alert("账号为空，请输入账号");
        return;
      } else if (this.dynamicValidateForm.email == "") {
        alert("邮箱为空，请输入邮箱");
        return;
      } else if (!reg.test(this.dynamicValidateForm.email)) {
        alert("邮箱格式不对，请输入正确的邮箱！");
        return;
      } else {
        ajax
          .goRister(
            this.username,
            this.password,
            this.dynamicValidateForm.email
          )
          .then(resp => {
            console.log(resp)
            if (resp.data.code) {
              this.$message("注册成功，请在10分钟之内查看邮件激活！");
              this.$router.push("/login");
            } else {
              this.errorMessage = resp.data.msg;
              this.$message({
                    showClose: true,
                    message: this.errorMessage,
                    type: "error"
                });
            }
          });
      }
    },
  }
};
</script>

<style lang='scss'>
.wiki-register-warp {
  padding-top: 40px;
  width: 100%;
  height: 100%;
  .wiki-register {
    width: 1280px;
    height: 100%;
    margin: 0 auto;
    &-content {
      width: 444px;
      height: 800px;
      margin: 0 auto;
      border: 1px solid rgba(228, 228, 228, 1);
      border-radius: 5px;
      padding: 30px;
      position: relative;
      .register-content-email {
        margin-top: 30px;
      }
      .register-content-username {
        margin-top: 22px;
      }
      .register-content-go-login {
        margin-top: 50px;
        font-size: 12px;
        color: #333;
        margin-left: 270px;
        display: inline-block;
      }

      .el-input {
        margin-top: 5px;
      }
      .el-button {
        width: 100%;
        margin-top: 5px;
      }

      .el-form {
        .el-form-item__content {
          left: -100px;
          width: 382px;
        }
        .el-form-item {
          margin-bottom: 6px;
        }
      }
    }
  }
}
</style>
